<template>
  <section
    class="bg-[#48D64C] text-black text-center w-full h-full py-14 md:py-20 px-5 md:px-20"
  >
    <h1 class="text-3xl md:w-[60%] md:text-3xl lg:text-7xl mx-auto">
      Join over 3 million members
    </h1>
    <div
      class="flex w-[100%] lg:w-full mx-auto overflow-x-scroll mt-12 md:mt-20 no-scrollbar gap-7"
      ref="slider"
    >
      <div
        class="flex flex-col flex-2 justify-between lg:flex-1 px-4 py-2 max-sm:boder-none lg:border-l-2 lg:border-black"
        v-for="(slide, index) in slides"
        :key="index"
      >
        <p class="text-left text-[#292929] lg:text-lg text-[1.1rem]">
          {{ slide.description }}
        </p>
        <div class="flex mt-8 justify-between items-center">
          <div class="flex flex-col text-left">
            <h3 class="text-[1.3rem] lg:text-[1.5rem]">
              {{ slide.fullName }}
            </h3>
            <p class="text-[0.7rem] lg:text-[1rem] text-[#292929]">
              {{ slide.role }}
            </p>
          </div>
          <div class="w-[50px] md:w-[45px] md:h-auto h-auto my-auto">
            <img :src="getPic(slide.img)" class="my-auto" alt="img" />
          </div>
        </div>
      </div>
    </div>
    <div class="grid justify-items-end mt-12">
      <div class="flex gap-6">
        <button
          @click="scrollBackward"
          class="w-[40px] h-[40px] md:w-[50px] md:h-[50px] rounded-full border border-black bg-white flex justify-center items-center"
        >
          <svg
            width="24"
            height="24"
            viewBox="0 0 24 24"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M19.5 12L4.5 12M4.5 12L11.25 18.75M4.5 12L11.25 5.25"
              stroke="#0F172A"
              stroke-width="1.5"
              stroke-linecap="round"
              stroke-linejoin="round"
            />
          </svg>
        </button>
        <button
          @click="scrollForward"
          class="w-[40px] h-[40px] md:w-[50px] md:h-[50px] rounded-full border border-black bg-white flex justify-center items-center"
        >
          <svg
            width="18"
            height="16"
            viewBox="0 0 18 16"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              fill-rule="evenodd"
              clip-rule="evenodd"
              d="M0.75 8C0.75 7.58579 1.08579 7.25 1.5 7.25L14.6893 7.25L9.21967 1.78033C8.92678 1.48744 8.92678 1.01256 9.21967 0.71967C9.51256 0.426777 9.98744 0.426777 10.2803 0.71967L17.0303 7.46967C17.3232 7.76256 17.3232 8.23744 17.0303 8.53033L10.2803 15.2803C9.98744 15.5732 9.51256 15.5732 9.21967 15.2803C8.92678 14.9874 8.92678 14.5126 9.21967 14.2197L14.6893 8.75L1.5 8.75C1.08579 8.75 0.75 8.41421 0.75 8Z"
              fill="#0F172A"
            />
          </svg>
        </button>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      slides: [
        {
          description: `RAFT has transformed my approach to finance. Their smart investing options have helped me grow my wealth, and their user-friendly platform makes managing my money a breeze. I've never felt more confident about my financial future.`,
          fullName: 'Robert Fox',
          role: 'Happy RAFT User',
          img: 'man-pic.png',
        },
        {
          description: `I can't express how grateful I am to RAFT. Their wealth management services have been a game-changer for my family's financial security. The expert guidance and personalized strategies have given us peace of mind, knowing that our future is in capable hands.`,
          fullName: 'Cameron Williamson',
          role: 'Happy RAFT User',
          img: 'man-pic2.png',
        },
        {
          description: `RAFT's financial planning services have been a lifeline for me. I always struggled with managing my money, but their team crafted a tailored plan that addressed my goals and concerns. It's been a game-changer in achieving my financial dreams`,
          fullName: 'Esther Howard',
          role: 'Happy RAFT User',
          img: 'man-pic3.png',
        },
      ],
    };
  },
  methods: {
    scrollForward() {
      this.$refs.slider.scrollBy({ left: 300, behavior: 'smooth' });
    },
    scrollBackward() {
      this.$refs.slider.scrollBy({ left: -300, behavior: 'smooth' });
    },
    getPic(name) {
      return require('@/assets/img-asset/' + name);
    },
  },
};
</script>
